



























































































































































































































































































































































































@import "../scss/mixins";

.vue-command {
  overflow-y: auto;
  overflow-x: hidden;

  .term-bar {
    background: #23262c;
    border: 1px solid #191a1b;
    display: block;
    justify-content: center;
    top: 0;
    width: 100%;
    display: flex;
  }

  .term-title {
    font-family: "Montserrat", sans-serif;
    font-size: 18px;
    margin: 12px;
    background: #23262c;
    color: white;
  }

  .term-std {
    background: #002b36;
    display: block;
    flex-direction: column;
    width: 100%;

    @extend .term-hist-container-fullscreen;
  }

  .term-cont {
    font-family: "Inconsolata", monospace;
    padding-right: 0.5rem;
    padding-bottom: 0.5rem;
    flex: 1;
  }

  .term-hist-container-fullscreen {
    display: flex;
    flex-direction: column;
    flex: 1;
    height: 100%;
  }

  .term-stdout {
    word-break: break-all;
    width: 100%;
  }
}
